<template>
  <div class="container-fluid">
    <table class="table ">
      <thead>
        <tr class="noselect">
          <th class="day timezone" style="width: 150px">
            <select
              v-model="timezone_selected"
              name="timezone"
              class="select-input form-control form-control-sm"
              id="timezone"
            >
              <option
                v-for="(tmz, index) in timezones"
                :key="index"
                :value="tmz"
                >{{ tmz }}</option
              >
            </select>
          </th>
          <th class="day" v-for="(day, index) in days" :key="index">
            {{ day }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(h, index) in hours" :key="index">
          <td class="hour noselect" @click="toggle_format = !toggle_format">
            {{ getTime(h.hour, h.minutes, toggle_format) }}
          </td>
          <td v-for="(boss, bIndex) in h.bosses" :key="bIndex">
            {{ boss }}
          </td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td colspan="8" class="text-right" style="padding: 5px">
            by Chizuko
          </td>
        </tr>
      </tfoot>
    </table>
  </div>
</template>

<script>
import { bosses } from "../../public/bosses.json";

export default {
  data() {
    return {
      selected_gmt: 0,
      toggle_format: true,
      timezone_selected: "GMT+2",
      timezones: [
        "GMT+0",
        "GMT+1",
        "GMT+2",
        "GMT+3",
        "GMT+4",
        "GMT+5",
        "GMT+5:30",
        "GMT+6",
        "GMT+7",
        "GMT+8",
        "GMT+9",
        "GMT+9:30",
        "GMT+10",
        "GMT+11",
        "GMT+12",
        "GMT-11",
        "GMT-10",
        "GMT-9",
        "GMT-8",
        "GMT-7",
        "GMT-6",
        "GMT-5",
        "GMT-4",
        "GMT-3:30",
        "GMT-3",
        "GMT-2",
        "GMT-1",
      ],
      // Mode 24hrs
      hours: bosses,
    };
  },
  methods: {
    getTime: function(hour, minute, format) {
      let time = new Date();
      if (this.signal === "+") {
        time.setHours(hour + parseInt(this.gmtHours));
        time.setMinutes(minute + parseInt(this.gmtMinutes));
      } else {
        time.setHours(hour - parseInt(this.gmtHours));
        time.setMinutes(minute - parseInt(this.gmtMinutes));
      }

      var hours = time.getHours();
      var minutes = time.getMinutes();
      var ampm = hours >= 12 ? "PM" : "AM";
      var strTime = "";
      if (format === true) {
        hours = hours % 12;
        hours = hours ? hours : 12; // the hour '0' should be '12'
        minutes = minutes < 10 ? "0" + minutes : minutes;
        strTime = hours + ":" + minutes + " " + ampm;
        return strTime;
      } else {
        hours = hours < 10 ? "0" + hours : hours;
        minutes = minutes < 10 ? "0" + minutes : minutes;
        strTime = hours + ":" + minutes;
        return strTime;
      }
    },
  },
  computed: {
    days() {
      return [
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
        "Sunday",
      ];
    },
    signal() {
      let tm = this.timezone_selected.split("GMT")[1][0];
      return tm;
    },
    gmtHours() {
      let tm = this.timezone_selected.split("GMT" + this.signal)[1];
      let hour = 0;
      if (tm.includes(":")) {
        hour = tm[0];
      } else {
        hour = tm;
      }
      return hour;
    },
    gmtMinutes() {
      let tm = this.timezone_selected.split("GMT" + this.signal)[1];
      console.log(tm);
      if (tm.includes(":")) {
        let min = tm.split(":")[1];
        console.log(min);
        return min;
      } else {
        return "0";
      }
    },
  },
};
</script>

